import React, { Component } from 'react';

export default class PageHeaderComponent extends Component {
	onClick = () => {
		alert('正在努力创作~');
	};
	render() {
		return (
			<header id="masthead" className="overlay animated from-bottom animation-on">
				<div className="site-branding text-center">
					<span>
						<img className="custom-logo avatar" src={'' + require('../../assets/20210105231911.jpg').default} alt="" />
					</span>
					<h3 className="blog-description" style={{ color: '#fff', marginTop: '20px' }}>
						搬运工-CV工程师
					</h3>
					<div className="top-social">
						<li>
							<span onClick={this.onClick.bind(this)}>
								<img src={require('../../assets/favicon.ico').default} style={{ padding: '6px' }} alt="" />
							</span>
						</li>
						<li>
							<span onClick={this.onClick.bind(this)}>
								<img src={require('../../assets/sf.ico').default} style={{ padding: '6px' }} alt="" />
							</span>
						</li>
						<li>
							<span onClick={this.onClick.bind(this)}>
								<img src="https://jensonhui.top/usr/themes/Akina/images/github.png" alt="" />
							</span>
						</li>
						<li>
							<span onClick={this.onClick.bind(this)}>
								<img src="https://jensonhui.top/usr/themes/Akina/images/bilibili.png" alt="" />
							</span>
						</li>{' '}
					</div>
				</div>
				<div className="animation-header">
					<div className="decor-wrapper">
						<svg id="header-decor" className="decor bottom" xmlns="https://www.w3.org/2000/svg" version="1.1" viewBox="0 0 100 100" preserveAspectRatio="none">
							<path className="large left" d="M0 0 L50 50 L0 100" fill="rgba(255,255,255, .1)"></path>
							<path className="large right" d="M100 0 L50 50 L100 100" fill="rgba(255,255,255, .1)"></path>
							<path className="medium left" d="M0 100 L50 50 L0 33.3" fill="rgba(255,255,255, .3)"></path>
							<path className="medium right" d="M100 100 L50 50 L100 33.3" fill="rgba(255,255,255, .3)"></path>
							<path className="small left" d="M0 100 L50 50 L0 66.6" fill="rgba(255,255,255, .5)"></path>
							<path className="small right" d="M100 100 L50 50 L100 66.6" fill="rgba(255,255,255, .5)"></path>
							<path d="M0 99.9 L50 49.9 L100 99.9 L0 99.9" fill="rgba(255,255,255, 1)"></path>
							<path d="M48 52 L50 49 L52 52 L48 52" fill="rgba(255,255,255, 1)"></path>
						</svg>
					</div>
				</div>
			</header>
		);
	}
}
