import React, { Component } from 'react';

import './player.css';
class PlayerComponent extends Component {
	state = {
		isPlaying: false,
		isCollapse: true,
	};
	playAction = () => {
		this.setState({
			isPlaying: !this.state.isPlaying,
		});
	};
	collapseAction = () => {
		this.setState({
			isCollapse: !this.state.isCollapse,
		});
	};
	render() {
		const { isPlaying, isCollapse } = this.state;
		return (
			<div id="myhkplayer" className={!isCollapse ? 'show' : ''}>
				<div className="player">
					<div className="top">
						<div className="cover">
							<div className="status" onClick={this.playAction.bind(this)}></div>
							<img src="https://y.gtimg.cn/music/photo_new/T002R300x300M000002y6LRK1U5vkr.jpg" alt="" className={isPlaying ? 'playing' : ''} />
						</div>
					</div>
					<div className="control">
						<i className="icon iconfont lphva-icon-weibiaoti--1" title="播放"></i>
						<i className="icon iconfont lphva-icon-xunhuan1" title="单曲循环"></i>
						<i className="icon iconfont lphva-icon-diyiyeshouyeshangyishou" title="上一首"></i>
						<i className="icon iconfont lphva-icon-zuihouyiyemoyexiayishou" title="下一首"></i>
					</div>
					<div className="musicbottom">
						<div className="volumecontrol">
							<div className="icon iconfont lphva-icon-shengyin"></div>
							<div className="volumeprogress">
								<div className="progressbg">
									<div className="progressbg1"></div>
									<div className="ts"></div>
								</div>
							</div>
						</div>
						<div className="playprogress">
							<div className="progressbg">
								<div className="progressbg1" style={{ width: '54%' }}></div>
								<div className="progressbg2" style={{ left: '0%', width: '100%' }}></div>
								<div className="ts" style={{ position: 'relative', left: ' 54%' }}></div>
							</div>
						</div>
						<div className="switch-playlist">
							<i className="fa fa-list-ul" title="播放列表"></i>
						</div>
						<div className="qhms">
							<i className="random fa fa-retweet" title="专辑循环"></i>
						</div>
						<div className="switch-ksclrc">
							<i className="fa fa-toggle-on" title="关闭歌词"></i>
						</div>
					</div>
				</div>
				<div className="switch-player">
					<i className="icon iconfont lphva-icon-jiantou2"></i>
				</div>
			</div>
		);
	}
}

export default PlayerComponent;
