import React, { Component } from 'react';
import HeaderComponent from '../../components/pageHeader';
import './detail.css';
import { response } from '../../posts';
const ReactMarkdown = require('react-markdown');

export default class DetailPage extends Component {
	state = {
		infos: {},
	};
	componentDidMount() {
		let { data = { list: [], count: 0 } } = response;
		let { list } = data;
		let { match } = this.props;
		let { id } = match.params;
		let infos = list.find((v) => v.id === id);
		this.setState({ infos }, () => {
			let { infos } = this.state;
			document.title = infos.title || '404页面';
			document.addEventListener('visibilitychange', () => {
				if (document.hidden) {
					document.title = '你怎么走啦~';
				} else {
					document.title = infos.title;
				}
			});
		});
	}
	renderContent() {
		let { infos } = this.state;
		return (
			<>
				<div id="main" className="detail">
					<div className="container markdown-body">
						<ReactMarkdown source={infos.mark_content} />
					</div>
				</div>
			</>
		);
	}
	onClick = () => {
		alert(1);
	};
	render() {
		return (
			<>
				<HeaderComponent />
				{this.renderContent()}
			</>
		);
	}
}
